import { useEffect, useState } from "react";
import { getChunks } from "../utils";
import data from "../data.json";
import ListStories from "./ListStories";
import Footer from "./Footer";

const storiesData = data.stories;

function App() {
  const [stories, setStories] = useState([]);
  const start = { current: 0 };
  const hit = { current: false };
  const limit = 5;

  const scrollHandler = () => {
    if (window.innerHeight + window.scrollY + 50 >= document.body.clientHeight && !hit.current) {
      hit.current = true;

      const moreStories = getChunks(storiesData, start.current);

      if (moreStories.length > 0) {
        setStories((currentStories) => {
          start.current += limit;
          const s = [...currentStories, ...moreStories];
          hit.current = false;
          return s;
        });
      }
    }
  };

  useEffect(() => {
    const newStories = getChunks(storiesData, start.current, limit);
    setStories([...stories, ...newStories]);
    start.current += limit;

    window.addEventListener("scroll", scrollHandler);

    return () => {
      window.removeEventListener("scroll", scrollHandler);
    };
  }, []);

  return (
    <div className="container">
      <ListStories stories={stories} />
      {stories.length > 0 && <Footer />}
    </div>
  );
}

export default App;
